<script>
import $ from "jquery"
export default {
  props: {
    productCount: {
      type: Object,
      default: {},
    }
  },
  data() {
    return {
      refresh: 0,
      rebirth: 0,
      relief: 0,
      repure: 0,
      clean: 0
    }
  },
  mounted() {
    $('.categoryList').hover(
      function() {
        $(this).addClass('categoryList-hover');
        $(this).find('.badge').addClass('bageCusColor-hover');
      },
      function() {
        $(this).removeClass('categoryList-hover');
        $(this).find('.badge').removeClass('bageCusColor-hover');
      }
    );
    $("#faceBookLogo,#instagramLogo,#youtubeLogo").hover(
      function(){
        $(this).removeClass('hoverout').addClass('hoverin');
      },
      function(){
        $(this).removeClass('hoverin').addClass('hoverout');
      }
    );
    $("#instagramLogo").hover(
      function(){
        $(this).find('.fa-instagram').addClass('insta');
      },
      function(){
        $(this).find('.fa-instagram').removeClass('insta');
      }
    );
  },
  watch: {
    productCount: {
      immediate: true,
      handler(newValue, oldValue) {
        console.log("in handler")
        console.log(newValue)
        console.log(oldValue)
        this.refresh = newValue.refresh
        this.repure = newValue.repure
        this.relief = newValue.relief
        this.rebirth = newValue.rebirth
        this.clean = newValue.clean
      }
    },
  },  
  // detect the props value from parent has changed
