<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <hr class="style-six w-100" />
    <div class="row">
        <div class="col-md-3">
            <sideBar :product-count="productCount"></sideBar>
        </div>
        <div class="col-md-9">
            <button
        id="btn-horizental"
        type="button"
        class="btn btn-outline-primary"
        @click.prevent="changeToHorizentalCard"
    >
        <i class="fas fa-list-ul"></i>
    </button>
    <button
        id="btn-vertical"
        type="button"
        class="btn btn-outline-secondary"
        @click.prevent="changeToVerticalCard"
    >
        <i class="fas fa-grip-horizontal"></i>
    </button>
    <hr class="style-six w-100" />
    <div v-if="verticalCard == false">
      <div class="row justify-content-center" v-for="(item,index) in products" :key="index">
        <div class="col-lg-4 align-self-center">
          <img
            :src="item.imageUrl"
            class="card-img"
            style="max-width:250px; height:auto;
            display: block;
            margin-left: auto;
            margin-right: auto;"
          />
        </div>
        <div class="col-lg-6 align-self-center">
          <div class="card-body text-center">
            <h5
              class="card-title"
              style="color:#999999;font-family: Arial, Helvetica, sans-serif;font-size:12px;font-style:italic"
            >{{item.category}}</h5>
            <router-link
              :to="{name:'OneProduct',params: {id:item.id}}"
              class="card-text"
              style="color:#333333;font-family: Arial, Helvetica, sans-serif;font-size:28px;"
              @click.native="emitOneProduct(item.title)"
            >
            {{item.title}}
            </router-link>
            <p
              class="card-text"
              style="color:#b0aa95;font-family: Arial, Helvetica, sans-serif;font-size:18px;"
            >NT${{item.price}}</p>
            <a
              href="#"
              class="btn btn-outline btnText"
              style="border-color: rgba(36,33,74,0.88);border-radius:0;"
              @click.prevent="addProductToCart(item.id,1)"
            >加入購物車</a>
          </div>
        </div>
        <hr class="style-six" />
      </div>
    </div>
    <div v-else>
      <div class="row row-cols-2 row-cols-lg-3">
        <div class="col mb-4" v-for="(item,index) in products" :key="index">
          <div class="card h-100 px-3">
            <img :src="item.imageUrl" class="card-img-top mb-3" />
            <div class="card-body text-center">
              <h5
                class="card-title"
                style="color:#999999;font-family: Arial, Helvetica, sans-serif;font-size:12px;font-style:italic"
              >{{item.category}}</h5>
              <router-link
                :to="{name:'OneProduct',params: {id:item.id}}"
                class="card-text"
                style="color:#333333;font-family: Arial, Helvetica, sans-serif;font-size:14px;"
                @click.native="emitOneProduct(item.title)"
              >
                {{item.title}}
              </router-link>
              <p
                style="color:#b0aa95;font-family: Arial, Helvetica, sans-serif;font-size:18px;"
              >NT${{item.price}}</p>
              <a
                href="#"
                class="btn btn-outline btnText"
                style="border-color: rgba(36,33,74,0.88);border-radius:0;"
                @click.prevent="addProductToCart(item.id,1)"
              >加入購物車</a>
            </div>
          </div>
        </div>
      </div>
    </div>
        </div>
    </div>
  </div>
</template>

<script>
import sideBar from "@/components/ClientSideBar.vue";
export default {
  props: {
        currentCategory: {
            type: String,
            default: 'refresh',
        },
  },
  components: {
      sideBar,
  },
  data() {
    return {
      products: [],
      productCount: {},
      isLoading: false,
      verticalCard: true,
    };
  },
  methods: {
    getProducts(category = this.currentCategory) {
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/products/all`;
      console.log(api);
      var vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log("response data:");
        console.log(response);
        if (response.data.success) {
          console.log(vm.products);
          vm.products = response.data.products.filter(function(item, index) {
            console.log("in the filter function : " + category);
            var re = RegExp(category, 'i');
            return re.test(item.category);
          });
          console.log(vm.products);
          vm.updateOtherProductCount(response.data.products);
          //vm.$parent.$emit('CategoryName',category);
        } else {
          console.log("Get CategoryProduct fail");
          console.log(response.data.message);
        }
        vm.isLoading = false;
      });
    },
    updateOtherProductCount(products) {
      var tmpObj = {};
      var tmpArr = [];
      tmpArr = products.filter(function(item, index) {
        var regExp = /rebirth/i;
        return regExp.test(item.category);
      });
      tmpObj.rebirth = tmpArr.length;
      console.log("length :");
      console.log(tmpObj.rebirth);

      tmpArr = products.filter(function(item, index) {
        var regExp = /refresh/i;
        return regExp.test(item.category);
      });
      tmpObj.refresh = tmpArr.length;
      console.log("length :");
      console.log(tmpObj.refresh);

      tmpArr = products.filter(function(item, index) {
        var regExp = /relief/i;
        return regExp.test(item.category);
      });
      tmpObj.relief = tmpArr.length;
      console.log("length :");
      console.log(tmpObj.relief);

      tmpArr = products.filter(function(item, index) {
        var regExp = /repure/i;
        return regExp.test(item.category);
      });
      tmpObj.repure = tmpArr.length;
      console.log("length :");
      console.log(tmpObj.repure);

      tmpArr = products.filter(function(item, index) {
        var regExp = /clean/i;
        return regExp.test(item.category);
      });
      tmpObj.clean = tmpArr.length;
      console.log("length :");
      console.log(tmpObj.clean);
      console.log("assgin new product count in category product");
      this.productCount = Object.assign({},tmpObj)
    //   this.$emit("productCount", tmpObj, this.currentCategory);

    },
    changeToHorizentalCard() {
      this.verticalCard = false;
    },
    changeToVerticalCard() {
      this.verticalCard = true;
    },
    emitOneProduct(product) {
        this.$parent.$emit('OneProductName',product);
        console.log("router link click");
    },
    addProductToCart(id,qty){
        console.log("here")
            console.log("begin to addToCart")
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/cart`;
            console.log(api);
            var vm = this;
            var tmpObj = {'product_id': id, 'qty': Number(qty)};
            vm.isLoading = true;
            this.$http.post(api,{ data: tmpObj }).then(response => {
                console.log("response data:");
                console.log(response);
                if (response.data.success) {
                    console.log(response.data);
                    vm.$bus.$emit('message:refreshCart');
                } else {
                    console.log("Add product to cart failed");
                    console.log(response.data.message);
                }
                vm.isLoading = false;
            });
        
    },
  },
  created() {
      console.log("!!!!category product is created");
      console.log(this.currentCategory);
    //this.getProducts();
  },
  beforeMount() {
      console.log("!!!!!before mount")
  },
  beforeUpdate() {
      console.log("!!!!!beforeUpdate")
  },
  updated() {
      console.log("!!!!updated")
  },
  mounted() {
      console.log("!!!!!moundted")
  },
  watch: {
        currentCategory: {
            immediate: true,
            handler(newValue,oldValue) {
                console.log("watch currentCategory change")
                console.log("new value " + newValue)
                console.log("old value " + oldValue)
                this.getProducts(newValue);
            }
        }
  },
  beforeCreate() {
      console.log("!!! beforeCreate for category")
  },
  beforeDestroy() {
      console.log("!!!!! before destroy")
  },
  destroyed() {
      console.log("!!!!!category product destroyed");
  },
};
</script>

